import tabControl from '@/components/tabControl-tag/tabControl-tag.vue';
export default {
	components: {
		tabControl
	},
	data() {
		return {
			items: ['首页', '市场补贴', '市场订单', "我的分享"],
			itemList: [],
			current: 0,
			currentPage: 0,
			listItems: ['商户订单', '市场订单'],
			listIndex: 0,
			listPage: 0,
			phone: uni.getStorageSync('phone'),
			userInfo: {},
			award: {},
			storeList: [],
			fenxiaoList: [],
			teamList:[]
		}
	},
	onLoad() {
		console.log(this.phone)
		//获取首页信息
		uni.request({
			url: this.$bashUrl + '/index.php/home/Api/user_infos',
			method: "POST",
			data: {
				phone: this.phone
			},
			success: res => {
				console.log(res)
				this.userInfo = res.data
			}
		})
		//获取补贴信息

		//获取订单

		//获取我的团队信息

	},
	onShow() {
		//获取市场用户
		uni.request({
			url: this.$bashUrl + '/index.php/home/Api/user_infos',
			method: "POST",
			data: {
				phone: this.phone
			},
			success: res => {
				console.log(res)
				this.userInfo = res.data
			}
		})
		//获取市场补贴
		uni.request({
			url: this.$bashUrl + '/index.php/home/Api/user_reward',
			method: "POST",
			data: {
				phone: this.phone
			},
			success: res => {
				console.log(res)
				this.award = res.data
			}
		})
		//获取市场订单
		this.fenxiaoList=[]
		this.storeList = []
			uni.request({
				url: this.$bashUrl + '/index.php/home/Api/t_log_list',
				method: "POST",
				data: {
					phone: this.phone,
					limit: 20
				},
				success: res => {
					console.log('-------award------')
					console.log(res)
					console.log('-------award------')
					res.data.forEach(item => {
						if (item.state == 0) {
							this.fenxiaoList.unshift(item)
						} else {
							this.storeList.unshift(item)
						}
					})
				}
			})
		//获取市场团队
		uni.request({
			url: this.$bashUrl + '/index.php/home/Api/user_team',
			method: "POST",
			data: {
				phone: this.phone
			},
			success:res=>{
				console.log(res)
				this.teamList = res.data
			},
			})
	},
	methods: {
		toAddStore(){
			uni.navigateTo({
				url:'../addStore/addStore'
			})
		},
		togetMoney(count,type) {
			uni.navigateTo({
				url: '../withdraw/withdraw?k_cash=' + count+"&type="+type
			})
		},
		toShow(msg) {
			if (this.userInfo.k_cash >= 100) {
				this.current = 1
			} else {
				uni.showToast({
					title: msg,
					icon: 'none'
				})
			}
		},
		toAward() {
			this.current = 0
		},
		getCode() {

		},
		toDetail() {
			this.current = 1
		},
		toOrder() {
			this.current = 2
		},

		toMyteam() {
			this.current = 3
		},
		onClickItem(e) {
			this.current = e.currentIndex
			
		},
		tabList(e) {
			console.log(e)
			this.listIndex = e
			
		},
		toEdit(phone) {
			if (phone) {
				uni.navigateTo({
					url: '../editor/editor?phone=' + phone
				})
			} else {
				let phone = uni.getStorageSync('phone')
				uni.navigateTo({
					url: '../editor/editor?phone=' + phone
				})
			}
		}

	}
}
